import React, { useEffect } from 'react';
import styles from './index.less';

interface ProgressProps {
  /**  */
  radius?: number;
  /**  */
  size?: number;
  /**  0-100 */
  progress: number;
  speed?: number;
  /**  */
  progressColor?: string;
  /**  */
  progressWidth?: number;
  /**  */
  orbitColor?: string | object;
  /**  */
  orbitWidth?: number;
  className?: string;
  /**  */
  fontColor?: string;
}
export function isObj(x: unknown): x is Record<string, unknown> {
  const type = typeof x;
  return x !== null && (type === 'object' || type === 'function');
}

export function format(rate: number) {
  return Math.min(Math.max(rate, 0), 100);
}
const Progress:React.FC<ProgressProps> = (props) => {
  const {
    progress,
    radius:R = 100,
    size = 500,
    speed:V = 0.1,
    progressColor = '#ccc',
    progressWidth = 10,
    orbitColor = 'red',
    orbitWidth = 10,
    fontColor = '#fff'
  } = props;

  useEffect(() => {
    const canvas:HTMLCanvasElement = document.getElementById('canvas') as HTMLCanvasElement;
    const ctx:CanvasRenderingContext2D = canvas.getContext('2d') as CanvasRenderingContext2D;

    canvas.width = canvas.height = size;
    const centerX = canvas.width/2;  // Canvasx
    const centerY = canvas.height/2; // Canvasy
    const rad = Math.PI*2/100; // 360100rad
    let speed = V; // 

    function gradientColor (color: any):CanvasGradient {
      const LinearColor = ctx.createLinearGradient(size, 0, 0, 0);
      Object.keys(color)
        .sort((a, b) => parseFloat(a) - parseFloat(b))
        .map((key) => LinearColor.addColorStop(
          parseFloat(key) / 100,
          color[key] as string
        ));
      return LinearColor;
    }

    // 
    function blueCircle(n: number){
      ctx.save();
      ctx.strokeStyle = isObj(orbitColor) ?
        gradientColor(orbitColor) : orbitColor as string; // 
      ctx.lineWidth = orbitWidth; // 
      ctx.lineCap = 'round'; // 
      ctx.beginPath(); // 
      // context.arc(xy/)
      ctx.arc(centerX, centerY, R , -Math.PI/2, -Math.PI/2 +n*rad, false);
      ctx.stroke(); // 
      ctx.closePath(); // 
      ctx.restore();
    }
    // 
    function whiteCircle(){
      ctx.save();
      ctx.beginPath();
      ctx.lineWidth = progressWidth; // 
      ctx.strokeStyle = progressColor;
      ctx.arc(centerX, centerY, R , 0, Math.PI*2, false);
      ctx.stroke();
      ctx.closePath();
      ctx.restore();
    }
    // 
    function text(n: number){
      const scale = R / 100;
      ctx.save(); // saverestorecanvas
      ctx.fillStyle = fontColor; // 
      ctx.font = `${40 * scale}px Arial`; // 
      // 
      ctx.fillText(n.toFixed(0)+'%', centerX-(25 * scale), centerY+(10 * scale));
      ctx.stroke(); // 
      ctx.restore();
    }
    // 
    (function drawFrame(){
      window.requestAnimationFrame(drawFrame);
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      whiteCircle();
      text(speed);
      blueCircle(speed);
      if(speed > format(progress)) {
        return;
      }
      speed += 0.1;
    }());
  })
  return (
    <div className={styles.content}>
      <canvas id='canvas' width='500' height='500'></canvas>
    </div>
  )
}

export default Progress;
